body {
  background: #fefefe;
  position: relative;
  overflow-x: hidden;
}

.container {
  max-width: 1370px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: content-box;
}

a {
  transition: all 0.3s;
}

.title {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 40px;
  text-transform: uppercase;
  color: #836851;
}

.input input {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #836851;
  border-radius: 20px;
  padding: 20px 30px;
  background: #fefefe;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.44444;
  letter-spacing: -0.04em;
  color: #9c8673;
}
.input input:placeholder-shown {
  color: #9c8673;
}
.input p {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 14px;
  letter-spacing: -0.04em;
  color: #fefefe;
  margin: 5px 0 0 30px;
}
.input__check {
  display: grid;
  grid-template-columns: 11px 1fr;
  gap: 6px;
  position: relative;
}
.input__check p {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 10px;
  letter-spacing: -0.04em;
  color: #fefefe;
}
.input__check p a {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #fefefe;
}
.input__check p a:hover {
  opacity: 0.7;
}
.input__check span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 11px;
  height: 11px;
  border: 1px solid #fefefe;
}
.input__check span svg {
  opacity: 0;
  transition: all 0.3s;
}
.input__check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  opacity: 0;
  z-index: 5;
}
.input__check input:checked + span svg {
  opacity: 1;
}

.btn__wht {
  border-radius: 20px;
  padding: 15px 35px;
  background: #fefefe;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.04em;
  color: #836851;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  transition: all 0.3s;
  border: 1px solid #fefefe;
}
.btn__wht svg {
  transition: all 0.3s;
}
.btn__wht path {
  stroke: #836851;
  transition: all 0.3s;
}
.btn__wht:hover {
  background: transparent;
  color: #fefefe;
}
.btn__wht:hover path {
  stroke: #fefefe;
}
.btn__braun {
  border-radius: 20px;
  padding: 20px 30px;
  background: #836851;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.04em;
  color: #fefefe;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  transition: all 0.3s;
  border: 1px solid #836851;
}
.btn__braun svg {
  transition: all 0.3s;
}
.btn__braun path {
  stroke: #fefefe;
  transition: all 0.3s;
}
.btn__braun:hover {
  background: transparent;
  color: #836851;
}
.btn__braun:hover path {
  stroke: #836851;
}
.btn__trans {
  border-radius: 20px;
  padding: 20px 30px;
  background: transparent;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.04em;
  color: #fefefe;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  transition: all 0.3s;
  border: 1px solid #fefefe;
}
.btn__trans svg {
  transition: all 0.3s;
}
.btn__trans path {
  stroke: #fefefe;
  transition: all 0.3s;
}
.btn__trans:hover {
  background: #fefefe;
  color: #836851;
}
.btn__trans:hover path {
  stroke: #836851;
}

.nav {
  padding: 60px 0 30px;
  background: linear-gradient(270deg, #7c6c60 0%, #ab988d 100%);
  border-radius: 0 0 30px 30px;
  z-index: 5;
  position: relative;
}
.nav__hum {
  display: none;
}
.nav__mob {
  display: none;
}
.nav__row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 50px;
}
.nav__logo {
  width: 100%;
  max-width: 255px;
  margin: 0 auto 0 0;
}
.nav__logo img {
  width: 100%;
}
.nav__text {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.04em;
  color: rgba(254, 254, 254, 0.7);
}
.nav__text a {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #fefefe;
}
.nav__text a:hover {
  color: rgba(254, 254, 254, 0.7);
}
.nav__soc {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.nav__soc a {
  display: block;
  width: 30px;
}
.nav__soc img {
  width: 100px;
}
.nav__main {
  border-radius: 0;
}
.nav__nav {
  margin: 30px 0 0;
  border-radius: 20px;
  padding: 10px 50px;
  background: #fefefe;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  position: relative;
}
.nav__nav_point {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.625;
  letter-spacing: -0.04em;
  color: #836851;
  transition: all 0.3s;
  cursor: pointer;
}
.nav__nav_point:hover {
  opacity: 0.7;
}
.nav__drop {
  display: none;
  position: absolute;
  width: 100%;
  left: 0;
  top: calc(100% + 17px);
  border-radius: 20px;
  padding: 20px 30px 40px 30px;
  background: #f3ece6;
  max-width: 1080px;
  gap: 10px;
  min-height: 600px;
  align-content: start;
}
.nav__drop_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.nav__drop_btn svg {
  transition: all 0.3s;
}
.nav__drop_btn.active svg {
  transform: rotate(-180deg);
}
.nav__drop.active {
  display: grid;
}
.nav__drop a {
  padding: 10px 20px;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.44444;
  letter-spacing: -0.04em;
  color: #9c8673;
  display: block;
  border-radius: 20px;
}
.nav__drop a:hover {
  background: #836851;
  color: #fefefe;
}
.nav__drop li {
  max-width: 738px;
}
.nav__drop li:hover .nav__aside {
  opacity: 1;
  pointer-events: all;
}
.nav__drop li:hover a {
  background: #836851;
  color: #fefefe;
}
.nav__aside {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  border-radius: 20px;
  padding: 20px 20px 46px 30px;
  background: #836851;
  width: calc(100% - 388px);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  opacity: 0;
  transition: all 0.3s;
  pointer-events: none;
  grid-template-rows: 1fr auto;
}
.nav__aside a {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.625;
  letter-spacing: -0.04em;
  color: #fefefe;
  padding: 0;
}
.nav__aside a:hover {
  opacity: 0.7;
}
.nav__aside p {
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.6875;
  letter-spacing: -0.04em;
  color: #fefefe;
}
.nav__aside ul {
  margin: 12px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}
.nav__aside div:nth-child(3), .nav__aside div:nth-child(4) {
  align-self: end;
}
.nav__aside_btn {
  display: grid;
  gap: 10px;
  max-width: 252px;
  grid-column: 2/3;
  grid-row: 2/3;
}
.nav__aside_btn button {
  width: 100%;
  font-size: 16px;
}
.nav__search {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.nav__search a {
  display: block;
}
.nav__search input {
  width: auto;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.625;
  letter-spacing: -0.04em;
  color: #836851;
  background: transparent;
  border: none;
  padding: 0;
  width: 80px;
}
.nav__search input::placeholder {
  color: #836851;
}
.nav .btn__braun {
  padding: 15px 35px;
  font-size: 14px;
}

.header {
  border-radius: 0 0 30px 30px;
  background: linear-gradient(270deg, #7c6c60 0%, #ab988d 100%);
  padding: 0 0 90px;
  position: relative;
  overflow: hidden;
}
.header__slider {
  overflow: visible;
  z-index: 1;
}
.header__slider h1,
.header__slider h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 75px;
  text-transform: uppercase;
  color: #fefefe;
  position: relative;
  transition: all 0.7s 0.3s;
  left: 0;
}
.header__slider img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 140px;
  width: calc(50vw + 545px);
  object-fit: cover;
  border-radius: 20px 0 0 20px;
  height: 100%;
  z-index: -1;
}
.header__info {
  border-radius: 20px 0 0 20px;
  backdrop-filter: blur(10px);
  background: linear-gradient(90deg, rgba(124, 108, 96, 0.9) 0%, rgba(171, 152, 141, 0.1) 100%);
  padding: 95px 80px 35px;
  position: relative;
  margin: 0 0 0 140px;
  max-width: 623px;
  width: 100%;
}
.header__info p {
  margin: 60px 0 0;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.625;
  letter-spacing: -0.04em;
  color: #fefefe;
  max-width: 400px;
}
.header__info .btn__wht {
  margin: 76px 0 0;
  font-size: 16px;
}
.header .swiper-slide {
  opacity: 0 !important;
  transition: all 0.3s;
}
.header .swiper-slide.swiper-slide-active {
  opacity: 1 !important;
  transition-delay: 0.2s;
}
.header .swiper-slide.swiper-slide-active h1,
.header .swiper-slide.swiper-slide-active h2 {
  left: -220px;
}
.header .swiper-button-prev,
.header .swiper-button-next {
  z-index: 2;
  width: 50px;
  height: 50px;
  background: #fefefe;
  border: 1px solid #fefefe;
  border-radius: 100%;
  transition: all 0.3s;
  transform: none;
  inset: auto;
  bottom: 35px;
  right: 0;
  margin: 0;
}
.header .swiper-button-prev::after,
.header .swiper-button-next::after {
  content: none;
}
.header .swiper-button-prev svg,
.header .swiper-button-next svg {
  width: 9px;
  height: 14px;
}
.header .swiper-button-prev svg path,
.header .swiper-button-next svg path {
  stroke-width: 2px;
  stroke: #836851;
  transition: all 0.3s;
}
.header .swiper-button-prev.swiper-button-disabled,
.header .swiper-button-next.swiper-button-disabled {
  opacity: 1;
  background: rgba(131, 104, 81, 0.1);
  backdrop-filter: blur(40px);
}
.header .swiper-button-prev.swiper-button-disabled path,
.header .swiper-button-next.swiper-button-disabled path {
  stroke: #fefefe;
}
.header .swiper-button-prev.swiper-button-next,
.header .swiper-button-next.swiper-button-next {
  bottom: 102px;
}
.header .swiper-pagination {
  margin: 0;
  transform: none;
  inset: auto;
  bottom: -51px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.header .swiper-pagination span {
  margin: 0 !important;
  display: block;
  width: 11px;
  height: 11px;
  border-radius: 100%;
  border: 1px solid #fefefe;
  transition: all 0.3s;
  background: transparent;
  opacity: 1;
}
.header .swiper-pagination span.swiper-pagination-bullet-active {
  background: #fefefe;
}

.service {
  padding: 100px 0;
}
.service__drop {
  margin: 50px 0 0;
  position: relative;
  z-index: 6;
}
.service__drop ul {
  position: absolute;
  width: max-content;
  top: calc(100% + 10px);
  left: 0;
  width: max-content;
  gap: 60px;
  row-gap: 18px;
  border-radius: 20px;
  padding: 20px 30px 40px 30px;
  background: #f3ece6;
  transform: all 0.3s;
  opacity: 0;
  pointer-events: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.service__drop ul a {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.44444;
  letter-spacing: -0.04em;
  color: #9c8673;
}
.service__drop ul a:hover {
  opacity: 0.7;
}
.service__drop.active button svg {
  transform: rotate(180deg);
}
.service__drop.active ul {
  opacity: 1;
  pointer-events: all;
}
.service__drop_spec ul {
  grid-template-columns: 1fr;
}
.service__row {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 53fr 42fr 31fr;
  gap: 20px;
}
.service__grid {
  margin: 30px 0 0;
  display: grid;
  grid-template-columns: 31fr 31fr 31fr 31fr;
  gap: 20px;
}
.service__img {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  display: block;
}
.service__img img {
  width: 100%;
  height: 300px;
  transition: all 0.3s;
  object-fit: cover;
}
.service__item {
  display: block;
}
.service__item p {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  letter-spacing: -0.04em;
  text-align: center;
  color: #836851;
  margin: 15px 0 0;
  transition: all 0.3s;
}
.service__item:hover img {
  transform: scale(1.1);
}
.service__item:hover p {
  opacity: 0.7;
}
.service__more {
  margin: 40px 0 0 auto;
  display: block;
  width: max-content;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 20px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-align: right;
  color: #836851;
}
.service__more:hover {
  opacity: 0.7;
}

.founder {
  background: linear-gradient(270deg, #7c6c60 0%, #ab988d 100%);
  padding: 20px;
}
.founder__block {
  border-radius: 20px;
  background: #fefefe;
  padding: 96px 0 35px;
  position: relative;
  z-index: 2;
}
.founder__block::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  width: 846px;
  height: 776px;
  bottom: 0;
  right: 0;
  background: url("../img/founder_desc.png") center no-repeat;
  background-size: cover;
}
.founder__qoute {
  margin: 70px 0 0 90px;
  max-width: 657px;
  position: relative;
  z-index: 2;
}
.founder__qoute p {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.44444;
  letter-spacing: -0.04em;
  color: #836851;
}
.founder__qoute .btn__braun {
  margin: 40px 0 0;
  font-size: 16px;
  max-width: 376px;
  width: 100%;
  padding: 20px;
}
.founder__qoute svg {
  position: absolute;
  top: -50px;
  left: -62px;
  z-index: -1;
}
.founder__list {
  margin: 147px 0 0;
  padding: 0 0 0 15px;
  border-left: 1px solid #836851;
}
.founder__list p {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.33333;
  letter-spacing: -0.04em;
  color: #836851;
}
.founder__list p strong {
  font-weight: 500;
}
.founder__list ul {
  margin: 8px 0 0;
}
.founder__list ul p {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.625;
  letter-spacing: -0.04em;
  color: #9c8673;
}
.founder__list ul li {
  display: grid;
  grid-template-columns: 24px 1fr;
}
.founder__list ul li::before {
  content: "";
  display: block;
  width: 5px;
  height: 5px;
  border-radius: 100%;
  margin: 9px auto 0;
  background: #9c8673;
}

.about {
  padding: 110px 0 71px;
}
.about__title {
  display: flex;
  justify-content: space-between;
  align-items: end;
  padding: 0 0 30px;
  border-bottom: 1px solid #836851;
  margin: 60px 0 0;
}
.about__title p {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  letter-spacing: -0.04em;
  color: #836851;
  max-width: 680px;
}
.about__title a {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 20px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #836851;
}
.about__title a:hover {
  opacity: 0.7;
}
.about__wrapper {
  display: grid;
  grid-template-columns: calc(50% - 10px) calc(50% - 10px);
  gap: 20px;
  margin: 30px 0 0;
}
.about__slider {
  width: 100%;
  position: relative;
}
.about__slider .swiper {
  width: 100%;
}
.about__slider img {
  width: 100%;
  border-radius: 20px;
}
.about__slider .swiper-button-prev,
.about__slider .swiper-button-next {
  z-index: 2;
  margin: 0;
  width: 50px;
  height: 50px;
  background: #836851;
  border: 1px solid #836851;
  border-radius: 100%;
  transition: all 0.3s;
  transform: none;
  inset: auto;
  top: calc(100% + 20px);
  left: calc(50% + 10px);
}
.about__slider .swiper-button-prev::after,
.about__slider .swiper-button-next::after {
  content: none;
}
.about__slider .swiper-button-prev svg,
.about__slider .swiper-button-next svg {
  width: 9px;
  height: 14px;
}
.about__slider .swiper-button-prev svg path,
.about__slider .swiper-button-next svg path {
  stroke-width: 2px;
  stroke: #fefefe;
  transition: all 0.3s;
}
.about__slider .swiper-button-prev.swiper-button-disabled,
.about__slider .swiper-button-next.swiper-button-disabled {
  opacity: 1;
  background: rgba(131, 104, 81, 0.1);
  backdrop-filter: blur(40px);
}
.about__slider .swiper-button-prev.swiper-button-disabled path,
.about__slider .swiper-button-next.swiper-button-disabled path {
  stroke: #836851;
}
.about__slider .swiper-button-prev.swiper-button-prev,
.about__slider .swiper-button-next.swiper-button-prev {
  left: auto;
  right: calc(50% + 10px);
}
.about__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 20px;
  row-gap: 30px;
}
.about__list li {
  padding: 0 0 0 15px;
  border-left: 1px solid #836851;
}
.about__list h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.25;
  text-transform: uppercase;
  color: #836851;
}
.about__list p {
  margin: 20px 0 0;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.44444;
  letter-spacing: -0.04em;
  color: #9c8673;
}

.work {
  padding: 100px 0 80px;
  position: relative;
  overflow: hidden;
}
.work__title {
  display: flex;
  justify-content: space-between;
  align-items: end;
  margin: 50px 0 0;
}
.work__title .service__drop {
  margin: 0;
}
.work__more {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 20px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-align: right;
  color: #836851;
}
.work__slider {
  margin: 40px 0 0;
  overflow: visible;
}
.work__slide {
  display: grid;
  grid-template-columns: 485px 1fr;
  background: #f3ece6;
  border-radius: 20px;
  overflow: hidden;
}
.work__result {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
}
.work__result div {
  position: relative;
}
.work__result img {
  width: 100%;
  height: 390px;
  object-fit: cover;
}
.work__result p {
  position: absolute;
  top: 0;
  left: 0;
  width: 94px;
  padding: 12px;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  letter-spacing: -0.04em;
